.popup {
  position: absolute;
  top: 100%;
  left: 0;
  background-color: white;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  padding: 10px;
  z-index: 10;
  display: flex;
  flex-direction: column;
  width: 400px;
}

.btn {
  flex: 1;
  margin: 0 10px;
  /* Add some spacing between buttons */
  padding: 15px 20px;
  /* Increase button height */
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  color: #fff;
}

.btn.accept {
  background-color: #D3D3D3 !important;
  color: black;
  /* Green */
}

.btn.reject {
  background-color: #D3D3D3 !important;
  color: black;
  /* Red */
}

.btn:hover {
  opacity: 0.9;
}

.buttons-container {
  display: flex;
  /* Arrange buttons next to each other */
  justify-content: space-between;
  /* Add space between buttons */
  margin-top: 0px;
}

.popup-button {
  background: #007bff;
  color: #fff;
  border: none;
  padding: 5px 10px;
  border-radius: 3px;
  cursor: pointer;
  margin: 2px 0;
}

.popup-button:hover {
  background: #0056b3;
}

.sidebar {
  width: 275px;
  padding: 20px;
  background-color: #120b5b;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  /* Floating shadow effect */
  position: relative;

  overflow-y: hidden;
  /* Allows scrolling if content overflows */
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background: white;
  padding: 20px;
  border-radius: 8px;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  max-width: 400px;
  width: 90%;
}

.modal-actions {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.modal-button {
  padding: 10px 20px;
  border: none;
  margin-left: 10px;
  margin-right: 10px;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
}

.modal-button.confirm {
  background-color: #d9534f;
  color: white;
}

.modal-button.cancel {
  background-color: #5bde62;
  color: white;
}

.modal-button:hover {
  opacity: 0.9;
}

